            "first_sentence": [
                "Bh\u00ed conai ar mhuintir Dursley in uimhir a ceathair Privet Drive, agus \u00e9 le smaiomh acu go raibh siad an-normalta go deo, agus iad br\u00e9a sasta de.",
                "Pan ddeffrodd Mr a Mrs Dursley ar y bore Mawrth tamp, llwyd hwnnw lle mae ein stori ni'n dechrau, doedd dim byd yn yr awyr gymylog tu allan i awrymu y byddai pethau rhyfedd ac anesboiadwy'n digwydd dros y wlad i gyd cyn bo hir.",
                "Dominus et Domina Dursley, qui vivebant in aedibus Gestationis Lingustrorum numero quattuor signatis, non sine superbia dicebant se ratione ordinaria vivendi uti neque se paenitere illius rationis.",
                "Bhi conai ar mhuintir Dursley in uimhir a ceathair privet Drive, agus e le maiomh acu go raibh siad an-normalta go deo, agus iad brea sasta de.",
                "Mr. and Mrs. Dursley, of number four, Privet Drive, were proud to say that they were perfectly normal, thank\r\nyou very much."
            ],
